<!-- 角色详情 -->
<template>
    <div class="role-detail">
        <base-title bread :breadList="[roleData.displayName]" title="角色"></base-title>
        <div class="content">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="角色信息" name="first" style="width: 400px">
                    <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules" >
                        <el-form-item label="角色名称:" prop="displayName">
                            <span v-show="!control">{{ addForm.displayName }}</span>
                            <el-input v-show="control" v-model="addForm.displayName"></el-input>
                        </el-form-item>
                        <el-form-item label="机构:" prop="orgId">
                            <span v-show="!control">{{ addForm.orgId }}</span>
                            <org-select  v-show="control"  :rootId="rootId"  :initText="orgSelectText" :refreshText="refreshText"   @node-select="handleOrgSelect"></org-select>
                        </el-form-item>
                        <el-form-item label="描述:" prop="describe">
                            <span v-show="!control">{{ addForm.describe }}</span>
                            <el-input v-show="control" v-model="addForm.describe"></el-input>
                        </el-form-item>
                        <el-form-item label="显示顺序:" prop="orderIndex">
                            <span v-show="!control">{{ addForm.orderIndex }}</span>
                            <el-input v-show="control" v-model="addForm.orderIndex"></el-input>
                        </el-form-item>
                        <el-form-item >
                            <input @click="save" class="button" type="button" v-show="control" value="保存">
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="成员列表" name="second">
                    <el-table style="margin-top:5px" :data="tableData" @selection-change="tableDataChange">
                        <el-table-column type="selection" width="55"></el-table-column>
                        <el-table-column prop="index" label="序号"></el-table-column>
                        <el-table-column prop="userName" label="姓名"></el-table-column>
                        <el-table-column prop="securityLevel" label="安全等级"></el-table-column>
                        <el-table-column prop="orgName" label="机构"></el-table-column>
                        <el-table-column label="操作" width="60">
                        </el-table-column>
                    </el-table>
                    <div class="footer">
                        <page class="page" @handleSizeChange="SizeMembers " @handleCurrentChange="CurrentMembers" :pageTotal="pageTotal"></page>
                    </div>
                </el-tab-pane>
            </el-tabs>
            <div class="icon">
                <btn-list v-show="opeState" @btn-click="roleEdit"  :model="{icon:'icon-edit',name:'编辑'}"></btn-list>
                <btn-list v-show="opeStateT" @btn-click="associated"  :model="{icon:'icon-add',name:'添加'}"></btn-list>
                <btn-list v-show="opeStateT" @btn-click="associateDell" :model="{icon:'icon-del',name:'删除'}"></btn-list>
            </div>
        </div>
        <!--弹框-->
        <div class="stu-yeaer-modal-add">
            <div class="modalAdd">
                <template>
                    <el-form :inline="true" :model="formInline" class="demo-form-inline">
                        <el-form-item label="" >
                            <el-input  v-model="formInline.name"></el-input>
                        </el-form-item>
                        <el-form-item label="组织结构" prop="institutions">
                            <div @click="structureTop">
                                <el-input
                                        v-model="formInline.queryFilterText"
                                        @blur="instBlur"
                                        placeholder="请选择或输入"
                                        @click.stop>
                                    <i slot="suffix"  class="iconfont icon-apartment"></i>
                                </el-input>
                            </div>
                            <div class="el-div-tree" v-if="queryStruct">
                                <el-tree
                                        class="filter-tree"
                                        :data="labelData"
                                        :props="defaultProps"
                                        default-expand-all
                                        :filter-node-method="filterNode"
                                        @node-click="queryAddIns"
                                        ref="tree">
                                </el-tree>
                            </div>
                        </el-form-item>
                        <el-form-item >
                            <el-checkbox v-model="formInline.checked">含下级</el-checkbox>
                        </el-form-item>
                        <el-form-item label="安全级别" label-width="80px">
                            <el-input style="width: 50px"  v-model="formInline.levelLeft"></el-input>
                        </el-form-item>
                        <el-form-item label="" label-width="40px">-</el-form-item>
                        <el-form-item label="">
                            <el-input style="width: 50px"  v-model="formInline.levelRnt"></el-input>
                        </el-form-item>
                        <el-button class="sear-btn" @click="searchAdd">查询</el-button>
                        <el-button class="clear-btn" type="text" >清空</el-button>
                    </el-form>
                    <el-table
                            @selection-change="handleSelectionChange"
                            :data="membersData"
                            style="width: 100%">
                        <el-table-column
                                type="selection"
                                width="55">
                        </el-table-column>
                        <el-table-column
                                prop="userName"
                                label="姓名">
                        </el-table-column>
                        <el-table-column
                                prop="workId"
                                label="工号">
                        </el-table-column>
                        <el-table-column
                                prop="orgName"
                                label="组织机构">
                        </el-table-column>
                        <el-table-column
                                prop="securityLevel"
                                label="安全级别"
                                width="100">
                        </el-table-column>
                    </el-table>
                    <div style="text-align: right;padding: 20px 0px">
                        <page class="page" @handleSizeChange="SizeChange" @handleCurrentChange="CurrentChange" :pageTotal="pageTotalAdd"></page>
                    </div>
                </template>
            </div>
        </div>

    </div>
</template>

<script>
import orgSelect from '@/components/org-select'
export default {
    data(){
        return {
            /*角色信息*/
            parId:'',//接收的id
            roleData:'',//校色信心
            control:false,
            orgSelectText:'',
            refreshText:false,
            rootId:'0',//机构id
            addForm:{
                displayName:'',
                orgId:'',
                describe:'',
                orderIndex:'',
            },
            formRules:{
                displayName: [{required: true, message: '请输入名称', trigger: 'blur'}],
                orgId:[{required: true, message: '请选择机构', trigger: 'blur'}]
            },
            opeState:true,
            opeStateT:false,
            /*成员列表*/
            pageIndex:1,
            pageSize:15,
            pageTotal:0,
            membersData:[],
            delInput:[],
            //头部机构
            pageIndexAdd:1,
            pageSizeAdd:10,
            pageTotalAdd:0,
            checkedT:'',
            queryFilterText:'',//绑定值
            labelData:[],//机构数据,
            ids:[],
            queryStruct:false,
            defaultProps: {
                children: 'children',
                label: 'displayName'
            },
            formInline:{
                name:'',
                queryFilterText:'',
                levelLeft:'',
                levelRnt:'',
                checked:'',
            },
            activeName:"first",
            tableData:[]
        }
    },
    components:{
        btnList:()=>import("@/components/btn-list"),
        page:()=>import("@/components/paging"),
        baseTitle:()=>import("@/components/title"),
        'org-select':orgSelect
    },
    //初始化
    created() {
        this.parId = this.$route.query.id;
        this.roleList();
        this.member();
        this.institutions();
    },
    methods:{
        filterNode(value, data) {
            if (!value) return true;
            return data.displayName.indexOf(value) !== -1;
        },
        //机构数据请求
        institutions(){
            let parameter = {
                level:0,
                onlyOrg:1,
                parentId:0
            };
            this.$api.institutions(parameter).then(res => {
                if(res.success == true){
                    this.labelData = res.data;
                }
            })
        },
        //查询角色
        roleList(){
            let parameter = {
                id:this.parId,
            };
            this.$api.roleList(parameter).then(res => {
                if(res.success == true){
                    this.addForm = {
                        displayName:res.data.displayName,
                        describe:res.data.notes,
                        orderIndex:res.data.orderIndex,
                    };
                    this.roleData = res.data;
                    this.addForm.orgId = this.roleData.orgName;
                }
            })
        },
        //角色信息编辑
        roleEdit(){
            this.control = true;
            this.addForm.name = this.roleData.orgName;
            this.addForm.orgId = this.roleData.orgId;
            this.orgSelectText = this.roleData.orgName;
            this.refreshText = !this.refreshText;
        },
        //编辑保存
        save(){
            let _this = this;
            _this.$refs["addForm"].validate((valid) => {
                if (valid) {
                    let parameter = {
                        id:this.parId,
                        displayName:_this.addForm.displayName,
                        notes:_this.addForm.describe,
                        orderIndex:_this.addForm.orderIndex,
                    };
                    _this.$api.roleEdit(parameter).then(res => {
                        if(res.success == true){
                            _this.$myLayer.successLayer(res.msg)
                            _this.roleList();
                            this.control = false;
                        }
                    })
                } else {
                    return false;
                }
            });
        },
        //点击树
        handleOrgSelect(val,callback){
            callback(true);
        },
        //按钮控制
        handleClick(val){
            if(val.index == '1'){
                this.opeState = false;
                this.opeStateT = true
            }else {
                this.opeState = true;
                this.opeStateT = false
            }
        },
        /*成员列表*/
        member(){
            let parameter = {
                roleId:this.parId,
                pageIndex:this.pageIndex,
                pageSize:this.pageSize,
            };
            this.$api.memberList(parameter).then(res => {
                if(res.success == true){
                    let array = [];
                    let list = res.data;
                    list.map((item,index)=>{
                        array.push(
                            Object.assign({
                                index:index+1
                            },item,{indexNum:'str'})
                        )
                    });
                    this.tableData = array;
                    //赋值分页总数
                    this.pageTotal = parseInt(res.totalDatas);
                }
            })
        },
        associateDell(){
            let _this = this;
            if(_this.delInput != ''){
                let parameter = {
                    accountIds:_this.delInput,
                    roleId:_this.parId
                };
                _this.$api.associateDell(parameter).then(res => {
                    if(res.success == true){
                        _this.$myLayer.successLayer(res.msg)
                        _this.member();
                    }
                })
            }else {
                _this.$myLayer.errorLayer('至少选择一条数据')
            }
        },
        tableDataChange(val){
            let delInput  = [];
            let i = 0;
            for (i = 0; i < val.length; i++) {
                delInput.push(val[i].id)
            }
            this.delInput  = delInput ;
        },
        SizeChange(val){
            this.pageSize = val;
            this.member();
        },
        CurrentChange(val){
            this.pageIndex = val;
            this.member();
        },
        //关联用户
        associated(){
            this.notMemberList();
            let _this = this;
            _this.$myLayer.formLayer("添加成员", $('.stu-yeaer-modal-add'), ['1000px'], function () {
                if(_this.ids != ''){
                    let parameter = {
                        accountIds:_this.ids,
                        roleId:_this.parId
                    };
                    _this.$api.associatedList(parameter).then(res => {
                        if(res.success == true){
                            _this.$myLayer.successLayer(res.msg)
                            _this.member();
                        }
                    })

                }else {
                    _this.$myLayer.errorLayer('至少选择一条数据')
                }
            })
        },
        notMemberList(){
            let parameter = {
                userName:this.formInline.name,
                orgId: this.queryFilterText,
                isIncSub:this.checkedT,
                securityLevelStart:this.formInline.levelLeft,
                securityLevelEnd:this.formInline.levelRnt,
                pageIndex:this.pageIndexAdd,
                pageSize:this.pageSizeAdd

            };
            this.$api.notMemberList(parameter).then(res => {
                if(res.success == true){
                    //赋值分页总数
                    this.pageTotalAdd = parseInt(res.totalDatas);
                    this.membersData = res.data;
                }
            })
        },
        //点击机构
        queryAddIns(val){
            this.queryFilterText = val.id;
            this.formInline.queryFilterText = val.displayName;
            this.queryStruct = false;
        },
        structureTop() {
            this.queryStruct = !this.queryStruct
        },
        instBlur(){
            this.queryStruct = false;
        },
        //分页
        SizeMembers(val){
            this.pageSizeAdd = val;
            this.notMemberList();
        },
        CurrentMembers(val){
            this.pageIndexAdd = val;
            this.notMemberList();
        },
        //查询
        searchAdd(){
            let checkedT = '';
            if(this.formInline.checked == true){
                checkedT = 0;
            }else {
                checkedT = 1;
            }
            this.checkedT = checkedT;
            this.notMemberList();
        },
        handleSelectionChange(val){
            let ids = [];
            let i = 0;
            for (i = 0; i < val.length; i++) {
                ids.push(val[i].id)
            }
            this.ids = ids;
        }

    }
}
</script>

<style lang="scss" scoped="scoped">
.role-detail {
    padding: 18px 24px;
    .content {
        margin-top: 15px;
        position: relative;
        .role-info {
            li {
                margin-top: 30px;
                &:first-child {
                    margin-top: 10px;
                }
            }
            span {
                &:first-child {
                    display: inline-block;
                    min-width: 4rem;
                    text-align: right;
                    margin-right: 2rem;
                }
            }
        }
        .icon {
            position: absolute;
            right: 0;
            top: 10px;
        }
    }
    .footer {
        text-align: right;
        margin-top: 25px;
        .page {
            display: inline-block
        }
    }
    .button{
        width: 75px;
        height: 36px;
        background: #4A80F6;
        color: #ffffff;
        border-radius: 4px;
        border: 1px solid #4A80F6;
    }
    .stu-yeaer-modal-add, .tu-yeaer-modal-edit {
        display: none;
    }
    .modalAdd {
        padding: 0px 32px;
        margin: 24px 0px;
    }
    .el-div-tree{
        position: absolute;
        top:45px;
        width: 100%;
        max-height: 200px;
        background-color: #fff;
        border: 1px solid #ebeef5;
        border-radius: 4px;
        -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
        z-index: 99999999!important;
        overflow: auto;
    }
}
</style>
<style>
.role-detail .el-breadcrumb .el-breadcrumb__item.title span {
    color: #303133;
    font-weight: bold;
}
</style>
